import React , {Component} from 'react';
import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import ErrorBoundary from '../components/ErrorBoundary';
import './App.css';

export default class App extends Component {
    constructor() {
        //this in constructor is not allowed before super()
        super();
        this.state = {
            robots: [],
            searchfield: ''
        }
    }

    componentDidMount(){
        //fetch api method - web api - MDN READ
        fetch('https://jsonplaceholder.cypress.io/users')
        .then(response => response.json())
        .then(users => {this.setState({ robots : users })})
    }
    //using arrow functions : does  not change the context (.this of the function)
    onSearchChange = (event) => {
        //sets the state in React
        this.setState({ searchfield : event.target.value })
        
    }

    render() {
        const filteredRobots = this.state.robots.filter(robot => {
            return robot.name.toLowerCase().includes(this.state.searchfield.toLowerCase());
        });
        if(this.state.robots === undefined || this.state.robots === null){
            return(
                <h1>Loading...</h1>
            )
        }else{
            return(
                <div className="tc">
                    <h1 className="f1">ROBOFRIENDS</h1>
                    <SearchBox searchChange={this.onSearchChange}/>
                    <Scroll>
                        <ErrorBoundary>
                             <CardList robots={filteredRobots}/>
                        </ErrorBoundary>
                    </Scroll>
                </div>
                )
        }
        
    }
}


//State (parent feeds)>> Prop (child recives)
//to use state in react use Class notation and renderf function