import React from "react";

//every props has children ie props.children
export default class Scroll extends React.Component{
    render() {
        //styles in react are kept under {{}} javascript expression notation with a Object
        return(
            <div style={{ overflow: 'scroll', border: '1px solid black' , height: '900px'}}>
                {this.props.children}
            </div>
        )
    }
}