import React from 'react';
import Card from './card';

//destructuring props to robots => similar to props = {robots}

//Line - 13 this is enclosed in {} as it is js variable 
const CardList = ({ robots }) => {
    /*if(true){
        throw new Error('Nooooo'); // throw error
    }*/
    return (
        <div>
            { //THIS IS JAVASCRIPT HENCE WE PACK IT IN CURLY BRACES INSIDE THE HTML 
            robots.map((user , i) => {
                return <Card key={i} 
                                id={user.id} 
                                name={user.name} 
                                email={user.email}
                                />
            })
            }
        </div>
    );
}

export default CardList;

